* {
  padding: 0;
  margin: 0;
  border: 0;
}

*,
*:before,
*:after {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

:focus,
:active {
  outline: none;
}

a:focus,
a:active {
  outline: none;
}

nav,
footer,
header,
aside {
  display: block;
}

html,
body {
  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1;
  font-size: 14px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

input,
button,
textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: 400;
}

/*--------------------*/


body{
  background-color: rgb(0, 0, 0);
  color: aliceblue;
  overflow-x: hidden;
}

#root{
  max-width: 1100px;
  margin: 0 auto;

}

.post{
  margin: 15px auto;
  max-width: 700px;
  border: 2px solid rgb(255, 255, 255);
  border-radius: 20px;
  padding: 10px 20px;

  cursor: pointer;
  display: flex;
  justify-content: space-between;
}

.post__title{
  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
  font-size: 20px;
  color: rgb(0, 255, 128); 
  padding-left: 4px;
  max-width: 550px;
  padding-bottom: 5px;
  border-bottom: 1px solid rgb(255, 255, 255);

  display:flex;
}
.post__title strong{
  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
  font-size: 20px;
  color: rgb(255, 255, 255);  
  border-bottom: 6px solid rgb(0, 0, 0);
  border-left: 9px solid rgb(0, 0, 0);
  margin-left: -4px;
}

.post__body{
  
  padding-top: 5px;
  font-size: 15px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  color: rgb(0, 255, 128); 
  padding-left: 27px;
  max-width: 550px;
}


.post__hedtitle{
  text-align: center;
}

.stitle{
  text-align: center;
  font-size: 35px;
  line-height: 40px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.postNone{
  text-align: center;
  font-size: 35px;
  line-height: 40px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}




.post-enter {
  opacity: 0;
  transform: translateY(-750px);
}
.post-enter-active {
  opacity: 1;
  transform: translateY(0px);
  transition: all 0.5s 	cubic-bezier(0.68, -0.55, 0.265, 1.55) 0s;
}
.post-exit {
  opacity: 1;
  transform: translateY(0px);
}
.post-exit-active {
  opacity: 0;
  transition: opacity 500ms ease-in;
  transform: translateX(-750px);
  transition: all 0.5s 	cubic-bezier(0.68, -0.55, 0.265, 1.55) 0s;
}

.pagination__wrapper{
  display: flex;
  justify-content: center;
}





.pagination {
  appearance: none;
  backface-visibility: hidden;
  background-color: #27ae60;
  border-radius: 8px;
  border-style: none;
  box-shadow: rgba(39, 174, 96, .15) 0 4px 9px;
  box-sizing: border-box;
  color: #fff;
  cursor: pointer;
  font-family: Inter,-apple-system,system-ui,"Segoe UI",Helvetica,Arial,sans-serif;
  font-size: 16px;
  font-weight: 600;
  letter-spacing: normal;
  line-height: 1.5;
  outline: none;
  overflow: hidden;
  padding: 1px 20px;
  position: relative;
  text-align: center;
  text-decoration: none;
  transform: translate3d(0, 0, 0);
  transition: all .3s;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  vertical-align: top;
  white-space: nowrap;

  height: 30px;

  margin: auto 5px;
  visibility: hidden;
}

.pagination:hover {
  background-color: #1e8449;
  opacity: 1;
  transform: translateY(0);
  transition-duration: .35s;
  color: #000000;
  
}

.pagination:active {
  transform: translateY(2px);
  transition-duration: .35s;
}

.pagination:hover {
  box-shadow: rgba(39, 174, 96, .2) 0 6px 12px;
}
.page__current{
  background-color: #1e8449;
  opacity: 1;
  transform: translateY(0);
  transition-duration: .35s;
  color: #000000;
}
.Coment{
  margin: 20px;
}
.combody{
  margin: 15px 0;  
}

.title__auth{
  margin-top: 50px;
  text-align: center;
  font-size: 30px;
}

.lastElement{
  position: relative;
  top: -700px;  
}