.modal{
    display: flex;
    justify-content: center;
    align-items: center;

    background-color: rgba(0, 0, 0, 0.589);

    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    z-index: 2;

    opacity: 0;
    visibility: hidden;
}
.bodys{
    display: inline;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 2;
}

.modal__content{
    display: flex;
    justify-content: center;
    align-items: center;

    background-color: rgb(0, 0, 0);

    width:600px;
    height: 50%;
    border-radius: 30px;
}

.active{
    opacity: 1;
    visibility: visible;

    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    z-index: 2;
}