body{
    position: relative;
}
.bodys{
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    top: 0px;
    bottom: 0;
    right: 0px;
    left: 0px;
    z-index: 5;

    width: 100%;
    height: 100%;
}
.wrapper{    
    flex-direction: column;
    padding: 10px 20px;
    background-color: rgba(240, 248, 255, 0.678);
    display: flex;
    justify-content: center;
    text-align: center;
    transform: translateX(100%);
    transition: all 0.4s ease;

    position: absolute;
    width: 100%;
    height: 100%;
}

.wrapper.active{
    display: flex;
    flex-direction: column;
    padding: 10px 20px;
    z-index: 2;
    background-color: rgb(240, 248, 255); 
    transform: translateX(0%);
    transition: all 0.4s ease;
    display: flex;
    justify-content: center;
    text-align: center;

    position: absolute;
    width: 100%;
    height: 100%;
}

@media(min-width:700px){
    .black{
        background-color: #000000;
        position: absolute;
        width: 200px;
        height: 100%;
        top: 0;
        left: 00px;
        z-index: 6;
    }
    .color{
        display: block;
        
    }
}

.wrapper button{
    position: absolute;
    bottom: 20px;
    right: 20px;
    z-index: 70;
}


.name {
    padding: 7px 0;
}
.name a{
    color: rgb(0, 0, 0);
    font-size: 30px;
    font-family: Arial, Helvetica, sans-serif;

}
.name a:hover{
    color: #1e8449;
}



.btns {
    width: 60px;
    height: 60px;
    position: fixed;
    z-index: 20000;
    top: 0px;
    border-radius: 50%;
    right: 80px;
    transition-duration: 1s;
    margin: 48px auto 48px auto;
    cursor: pointer;
    background-color: rgba(240, 248, 255, 0.342);
}


.btns span {
    height: 4px;
    width: 40px;
    background-color: #000000;
    border-radius: 20px;
    position: absolute;
    top: 27px;
    right: 10px;
    transition-duration: .25s;
    transition-delay: .25s;    
}
.btns span:before {
    left: 0;
    position: absolute;
    top: -10px;
    height: 4px;
    width: 40px;
    background-color: #000000;
    content: "";
    border-radius: 20px;
    transition-duration: .25s;
    transition: transform .25s, top .25s .25s;
}
.btns span:after {
    left: 0;
    position: absolute;
    top: 10px;
    height: 4px;
    width: 40px;
    background-color: #000000;
    content: "";
    border-radius: 20px;
    transition-duration: .25s;
    transition: transform .25s, top .25s .25s;
}
.btns.open span {
    transition-duration: 0.1s;
    transition-delay: .25s;
    background: transparent;
}
.btns.open span:before {
    transition: top .25s, transform .25s .25s;
    top: 0px;
    transform: rotateZ(-45deg);
}
.btns.open span:after {
    transition: top 0.4s, transform .25s .25s;
    top: 0px;
    transform: rotateZ(45deg);
}













